<template>
  <div class="mcontaner">
    <Header></Header>
    <div class="block">
      <el-timeline>
        <el-timeline-item
          v-bind:timestamp="blog.created"
          placement="top"
          v-for="blog in blogs"
          :key="blog.id"
        >
          <el-card>
            <h4>
              <router-link
                :to="{ name: 'BlogDetail', params: { blogId: blog.id } }"
                >{{ blog.title }}</router-link
              >
            </h4>
            <p>{{ blog.description }}</p>
          </el-card>
        </el-timeline-item>
      </el-timeline>
    </div>
    <el-pagination
      class="mpage"
      background
      layout="prev, pager, next"
      :current-page="currentPage"
      :page-size="pageSize"
      @current-change="page"
      :total="total"
    >
    </el-pagination>
  </div>
</template>

<script>
import Header from "../components/Header.vue";

export default {
  name: "Blogs.vue",
  components: { Header },
  data() {
    return {
      blogs: {},
      currentPage: 1,
      total: 1,
      pageSize: 5,
    };
  },
  methods: {
    page(currentPage) {
      const _this = this;

      this.$axios.get("blogs?currentPage=" + currentPage).then((res) => {
        //console.log(res);
        _this.blogs = res.data.data.records;
        _this.currentPage = res.data.data.current;
        _this.total = res.data.data.total;
        _this.pageSize = res.data.data.size;
      });
    },
  },
  mounted() {
    this.page(1);
  },
};
</script>

<style scoped></style>
